<template>
  <div id="main">
    <div class="resetAnalytics" id="resetAnalytics"></div>
      <div class="modal">
        <transition appear name="modal" :key="vidSrc+``">
          <video :muted="$settings.Background_Vids_Muted" playsinline width="100%" height="100%" :key="modalVid" ref="bg_video" id="bg_video" loop="true">
            <source :src="modalVid" :key="modalVid" type="video/mp4"> 
          </video>
        </transition>
      </div>
    <div class="top_area">
			<!-- Header Area that holds a Top, Left and Right Nav area as well as a Center Widget Area - width: 100%, height: 12.5% (480px), z-index: 1 -->
				<div class="top_area_wrapper" ref="top_area_wrapper"  v-show="JSON.stringify(activeDirectory[activeDirectory.length - 1]) !== JSON.stringify(homeDirectory)">

                    <!-- I have to do index.html for now when we compile -->
					<div class="top_side_nav top" id="top_side_nav_left" >
					<!-- Top Left Nav area to put invisible button to get back to main menu - width:10%, height: 80% -->
						<!-- ** ADD CONTENT OR CODE HERE ** -->	 
						<div class="button" id="goback">
              <!-- <a class="homeinvisible" v-show="nestedRank === 1"></a> -->
              <font-awesome-icon :icon="['fas', 'fa-circle-left']" class="close"/>
            </div>						
					</div > <!-- End #top_side_nav_left -->

					<a class="top_side_nav top" id="top_side_nav_right">
            <!-- @click="[$store.commit('setActiveDirectory', homeDirectory), $store.commit('setNestedRank', 0)] -->

						<a href="./index.html" class="button" id="homedir">
              <font-awesome-icon :icon="['fas', 'fa-house-chimney']" class="close"/>
            </a>						
					</a> <!-- End #top_side_nav_right -->
				</div> <!-- End .top_area_wrapper -->
			</div> <!-- End .top_area -->
			
			<!-- Main Content Area that holds the selectable Tiles of content and the Secondary Content Areas - 12.5% from the Top -->
			<div class="main_area">
			
        <!-- Begin: Top widget area -->
        <div class="secondary_content_area_wrapper">
          <div class="secondary_content_area">
            <div id="secondary_content_widgetarea_left" class="secondary_content_widgetarea">
            </div> 
            <div id="secondary_content_widgetarea_center" class="secondary_content_widgetarea">
            </div> 
            <div id="secondary_content_widgetarea_right" class="secondary_content_widgetarea">
            </div>
          </div>
        </div>
				<!-- End: Top widget area -->

				<div class="tile_carousel_wrapper">
				<!-- Wrapper for the Tile Carousel - 23% from Top of Main Nav Area -->
					<span class="tile_carousel_nav_wrapper">

            <!-- Begin: Right arrow -->
						<div 
              class="tile_carousel_nav" 
              id="tile_carousel_nav_left" 
              :style="carouselPos === 0 ? `filter: grayscale(1); pointer-events: none;` : `pointer-events: auto;`" 
              >
							<img src="./images/tile_carousel_nav_left.png" v-show="activeDirectory[activeDirectory.length - 1].length > tilesPerPage">
						</div> 
            <!-- End: Right arrow -->	

            <!-- Begin: Right arrow -->
						<div class="tile_carousel_nav" 
                id="tile_carousel_nav_right" 
                :style="carouselPos < carousels - 1 ? `pointer-events: auto;` : `filter: grayscale(1); pointer-events: none;`" 
                >
							<img src="./images/tile_carousel_nav_right.png" v-show="activeDirectory[activeDirectory.length - 1].length > tilesPerPage">
						</div>
            <!-- End: Right arrow -->	

					</span> 
          <!-- <div v-else class="spacer">

          </div> -->
          <!-- End .tile_carousel_nav_wrapper -->

        <router-link id="screensaver" :to="`/screensavervideo#00-ScreenSaver`"></router-link>

        <div id="tile_carousel_page_1" :style="`transform: translateX(-${carouselTransform}px); transition: all 1s ease;`" :key="nestedRank+activeDirectory[activeDirectory.length - 1]">
          <!-- Number of carousel slides -->
          <div v-for="(carousel, j) in carousels" :key="j" class="tile_carousel_page" :id="`carousel-${j}`">
            <!-- Loop for the carousel videos (always should be max {tilesPerPage} videos per 1 slide) -->
            <transition appear name="carousel"  v-for="(item, i) in activeDirectory[activeDirectory.length - 1].length < tilesPerPage ? activeDirectory[activeDirectory.length - 1] : tilesPerPage" :key="i + activeDirectory[activeDirectory.length - 1]">
              <a class="tile" 
              :id="`clicktile_${i + j * tilesPerPage}`" v-if="activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage]"
              :style="`background: ${$settings.Border_Color}`">
                <!-- Tile Container -->
                  <!-- @click="tileClickEvents(activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage], i + j * tilesPerPage)" -->
                  <span class="tile_content_link_wrapper" 
                  target="_self" 
                  :ref="`vid_${i + j * tilesPerPage}`">
                    <a class="tile_content_thumb" 
                    :id="`clicktile_${i + j * tilesPerPage}`"
                    v-show="activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage] && activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Thumbnail[0].url &&
                    activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Thumbnail.toString().length > 0">

                    <component 
                    :is="activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage] && activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Thumbnail[0].url.includes('.mp4') ? `video` : `img`"
                    :src="activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Thumbnail[0].url">
                    <source :src="activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Thumbnail[0].url" type="video/mp4" />
                    </component>
                    </a>

                    <p class="tile_title" 
                    :id="`clicktile_${i + j * tilesPerPage}`" v-show="activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage] && activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Title"
                    :style="`color: ${$settings.Text_Color}`">

                      {{activeDirectory[activeDirectory.length - 1][i + j * tilesPerPage].fields.Title}}
                    </p>
                  </span>
                  <!-- End .tile_content_link_wrapper -->

                </a>
            </transition>
            <!-- END CONTENT TILE - 1 -->
          </div>
        </div>
        <!-- End .tile_carousel_page -->
      </div>
      <!-- End .tile_carousel_wrapper -->

      <!-- Begin: Bottom widget area -->
      <div class="bottom_area">
        <div class="bottom_content_area_wrapper">
          <div id="bottom_content_widgetarea_left" class="bottom_content_widgetarea">
          </div>
          <div id="bottom_content_widgetarea_center" class="bottom_content_widgetarea">
          </div>
          <div id="bottom_content_widgetarea_right" class="bottom_content_widgetarea">
          </div>
        </div>
      </div>  
      <!-- En: Bottom widget area -->
      
    </div>
    <!-- End .main_area -->
  </div>
</template>

<script>
  import Analytics from "./scripts/analytics.js";
  import {Carouseljs} from "./scripts/carousel.js";
  const Carousel = new Carouseljs();
  import {mapState} from 'vuex';
  export default {
    name: "App",
    components: {
    },
    props: {
      route: {
        type: Object,
        default: () => {}
      },
      clickedTiles: {
        type: Array,
        default: () => []
      },
      tilesPerPage: {
        type: Number,
        default: 6
      },
      differentBGOnNested: {
        type: Boolean,
        default: false
      },
      inactiveState: {
        type: Object,
        default: () => {}
      }
    },
    watch: {

      timeElapsed: {
        handler(e) {
          if (e) {

            // Analytics for time elapsed
            this.timesTimeElapsed++;
            // this.$analytics.track({event: "timesIdle", description: "The number of times the Display goes idle and how long each time is", times: this.timesTimeElapsed}, (e) => {
            //     Analytics.write("timesIdle", {times: this.timesTimeElapsed});
            //   });

            // Go to screen saver
            if (this.inactiveState.screensaver) document.getElementById("screensaver").click();

            // Play random video
            if (this.inactiveState.play_random_tile) this.$refs[`vid_${[Math.floor(Math.random() * this.activeDirectory[0].length - 1)]}`][0].$el.click();

            // Go to home page
            if (this.inactiveState.go_to_home_page) {
              if (document.getElementById("top_side_nav_right")) {
                clearInterval(this.timer);
                this.$store.commit('setActiveDirectory', this.homeDirectory);
                this.$store.commit('setNestedRank', 0);
                if (document.getElementById("resetAnalytics")) {
                  document.getElementById("resetAnalytics").click();
                }
              }
            }
            
          }
        }
      },
      // Watches the carousel position
      carouselPos: {
        handler() {
          this.$nextTick(() => {
            // this.carousel();
            this.handleTileSpacing();
              Carousel.pause().then(() => { 
                setTimeout(() => {
                  Carousel.play()
                }, 1000)
              });
            
          })
        },
        immediate:true,
        deep:true
      },
      // Watches changes in nested pages navigating
      activeDirectory: {
        handler() {
          // if in nested, the background video is different
          if (this.differentBGOnNested) {
            // if on nested page, try nested bg video if any and default to the main bg if none
            if (this.homeDirectory.toString() !== this.activeDirectory[this.activeDirectory.length - 1].toString()) {
              this.modalVid = require(`./media/Background/Nested Background/${require.context('./media/Background/Nested Background', false, /\.mp4|.png$/).keys()[0].split("./")[1]}`);
              console.log(this.modalVid);
              // if on home page, play the main bg video
            } else {
              this.modalVid = require(`./media/Background/Main Background/${require.context('./media/Background/Main Background', false, /\.mp4|.png$/).keys()[0].split("./")[1]}`);
            } 
          }
          this.$nextTick(() => {
            this.handleTileSpacing();
            if (this.inactiveState.enabled) this.inactiveStateTimer();
            // Default reset carousel
            this.carousel();
            // this.$store.commit("setCarouselPos", 0);
            // this.$store.commit("setCarouselTransform", 0);

              Carousel.pause().then(() => { 
                setTimeout(() => {
                  Carousel.play()
                }, 1000)
              });
            
          })
        },
        immediate:true,
        deep:true
      }
    },
    computed: {
      ...mapState(['carouselTransform', 'carouselPos', 'clickedTilesArray', 'activeDirectory', 'homeDirectory', 'nestedRank', 'activePage'])
    },
    methods: {
      getLink(item) {
        // If tile has nested page
        if (item.Nested_Children) {
          return `#nestedpage=${item.ID}`;
        }
        // If tile leads to video page
        if (item.Content && item.Content[0].url && item.Content[0].url.includes(".mp4")) {
          document.getElementById("main").removeEventListener("click", this.handleClick);
          clearInterval(this.timer);
          return `video=${item.ID}`;
        }
        return undefined;
      },

      handleTileSpacing() {
        this.$nextTick(() => {
          let activeTiles = this.activeDirectory[this.activeDirectory.length - 1].length;
          if (document.getElementsByClassName("tile_carousel_page")[this.carouselPos] && (this.tilesPerPage === 5 || activeTiles === 5 || activeTiles === 6 ||  this.tilesPerPage === 6) && document.getElementById("carousel-0").children.length > 4) {
            this.$nextTick(() => {
              if (document.getElementsByClassName("tile_carousel_page")[this.carouselPos].getElementsByClassName("tile")[0])
                document.getElementsByClassName("tile_carousel_page")[this.carouselPos].getElementsByClassName("tile")[0].style = `margin-left: 80px; background: ${this.$settings.Border_Color || `rgb(16, 158, 217)`}`;
              if (document.getElementsByClassName("tile_carousel_page")[this.carouselPos].getElementsByClassName("tile")[2])
                document.getElementsByClassName("tile_carousel_page")[this.carouselPos].getElementsByClassName("tile")[2].style = `margin-right: 80px; background: ${this.$settings.Border_Color || `rgb(16, 158, 217)`}`;
            })
          }
        })
      },


      goBackNestedPage() {
        // Go out of the nested pages
        this.$store.commit('setNestedRank', this.nestedRank - 1);

        if (this.activeDirectory.length > 1) {
          this.$store.commit("goBack");
        }

        // Make sure route is matching when you reach the home directory
        if (this.homeDirectory.toString() === this.activeDirectory[this.activeDirectory.length - 1].toString()) {
          // this.$router.push("./index.html");

          // Enable this only if the background video on nested and home pages are DIFFERENT. Helps with memory leaks
          window.location.href = "./index.html";
          // this.$store.commit('setActiveDirectory', this.homeDirectory);
          // this.$store.commit('setNestedRank', 0);
        }
      },

      handleClick(e) {
        // Just resume playing video if it's paused, upon click event
        // if (this.$refs.bg_video.paused) this.$refs.bg_video.play();
        // If the inactive state is enabled, star the inactive state timer
        if (this.inactiveState.enabled) this.inactiveStateTimer();
        // Special for admin to reset analytics
        if (e.target.className === "resetAnalytics") {
          this.$store.commit("resetAnalytics");
          this.$store.commit("setActivePage", "ScreensaverPage");
          location.reload();
        }
        if (e.target.id === "homedir") {
          e.preventDefault();
          this.$store.commit("setCarouselPos", 0);
          this.$store.commit("setCarouselTransform", 0);
          this.$store.commit("setActiveDirectory", this.homeDirectory);
          window.location.href = "./index.html";
          return;
        }

        e.stopPropagation();

        // Go back up nested page
        if (e.target.id === "goback") {
           e.preventDefault();
          this.goBackNestedPage();
        }
        // If right arrow
        if (e.target.id === "tile_carousel_nav_right") {
          Carousel.carouselright(e);
        }
        // If left arrow
        if (e.target.id === "tile_carousel_nav_left") {
          Carousel.carouselleft(e);
        }
        // If tile
        if (e.target.id.includes("clicktile")) {
          var i = e.target.id.split("_")[1];
          var clickedTile = this.activeDirectory[this.activeDirectory.length - 1][i];
          this.$nextTick(() => {
            this.tileClickEvents(clickedTile, i);
          })
        }
      },

    // tile click events that exclude videos
    tileClickEvents(item, i) { 

      this.$nextTick(() => {
        // If there's 5 or 6 tiles, center then so there isn't a widow tile
        this.handleTileSpacing();
      })

      // Could be inactive tile that does absolutely nothing
      if (!item.fields.Nested_Children && !item.fields.Content && !item.fields.is_screenshot_page && !item.fields.HTML) return;

      this.$refs.bg_video.pause();
      

        this.$nextTick(() => {
          
              // if it's a 3D viewer
              if (item.fields["3D_Content"]) {
                // if it's an autodesk viewer
                if (item.fields["3D_Content"].includes("autodesk?")) {
                  this.$router.push(item.fields["3D_Content"]);
                  return;
                }

                if (item.fields["3D_Content"].includes("sketchfab?")) {
                  this.$router.push(item.fields["3D_Content"]);
                  return;
                }

              // if local 3D files, view in threeJS
              this.$router.push(`/view3d_screen#${item.fields["3D_Content"]}`);
            }

            // If it is Nested Page
            if (item.fields.Nested_Children && item.fields.Nested_Children.length > 0) {
              this.$store.commit("setActiveDirectory",[...this.$videos.filter(i => item.fields.Nested_Children.indexOf(i.fields.ID) > -1)]);
              this.$store.commit('setNestedRank', this.nestedRank + 1);
          
            }
            // If it is Screenshot / Turn me into hologram page
            if (item.fields.is_screenshot_page === "yes") {
              document.getElementById("main").removeEventListener("click", this.handleClick);
              clearInterval(this.timer);
              this.$router.push("screenshot_screen");
              return;          
            }

            if (item.fields.HTML) {
              document.getElementById("main").removeEventListener("click", this.handleClick);
              clearInterval(this.timer);
              this.$router.push(`/iframe#${item.fields.ID}`);
              return;   
            }
            
            // Update link property. If there is no link, don't even try pushing any link at all
            if (this.getLink(item.fields) !== undefined) {
              // When you click a video mid-Activation video, it will cancel it altogether. Have to double check if we're doing this or not
              this.$router.push(this.getLink(item.fields));
            }
            

            // Clicked tiles analytics
            var clickedTiles = this.clickedTilesArray;
            clickedTiles[i]++;
            this.$store.commit('setClickedTiles', clickedTiles);
            this.$analytics.track({event: "tileClick", property: `Tile_${i}`,  result: clickedTiles[i], _id: `tileClick_Tile_${i}`}, (e) => {
              Analytics.analytics(e.payload.properties);
            })
        })
      },

      inactiveStateTimer() {
        // If NOT on home page
        this.timeElapsed = false;
        // if (JSON.stringify(this.activeDirectory[this.activeDirectory.length - 1]) !== JSON.stringify(this.homeDirectory)) {
          clearTimeout(this.timeout);
          this.timeElapsed = false;
          this.timeout = setInterval(() => {
            this.timeElapsed = true;
          }, this.inactiveState.milliseconds);
        // }
      },

      carousel() {

      // Calculates how many carousel slides based on the amount of videos (tilesPerPages of {tilesPerPage})
        this.carousels = Math.ceil(this.activeDirectory[this.activeDirectory.length - 1].length / this.tilesPerPage);
        // Initiate the carousel sliding functionality
        Carousel.init(this.activeDirectory[this.activeDirectory.length - 1], this.tilesPerPage);
      },
      introVideoEnded() {
        // this.$store.commit("setIntroVideoPlayed", true);
        this.introVideoPlayed = true;
        // this.modalVid = require(`./media/Background/Main Background/${require.context('./media/Background/Main Background', false, /\.mp4|.png$/).keys()[0].split("./")[1]}`);
      },
      randomizeVideo(num) {
        this.modalVid = require(`./media/Background/Main Background/${this.dir[num]}`);
        this.$refs.bg_video.removeEventListener("ended", () => this.handleEnded(num));
        this.$nextTick(() => {
            this.$refs.bg_video.addEventListener("ended", () => this.handleEnded(num));
            if (this.activePage === "App") this.$refs.bg_video.play();
        })
      },
      integer() {
        if (this.randomized) {
            var num = Math.floor(Math.random() * this.dir.length);
            while (this.videosPlayed.indexOf(num) > -1) {
                num = Math.floor(Math.random() * this.dir.length);
            }
            return num;
        } else {
            this.counter++
            return this.counter;
        }
          
      },
      handleEnded(num) {
        this.videosPlayed.push(num)
        if (this.videosPlayed.length === this.dir.length) {
            if (this.loopAll) {
                this.videosPlayed = [];
            } else {
                return;
            }
        }
        this.$nextTick(() => {
            this.randomizeVideo(this.integer());
        })
      },

    },
    data() {
      return {
        // Declare default # of carousel slides to at least 1
        carousels: 1,
        // Active modal video playing
        modalVid: null,
        subFolder: null,
        timeElapsed: false,
        timesTimeElapsed: 0,
        timer: null,
        timeout:null,
        vidSrc: {
          src: null
        },
        playPromise:null,
        videosPlayed:[],
        randomized:true,
        counter: -1,
        dir: require.context('./media/Background/Main Background/', false, /\.mp4|.png$/).keys().length > 0 ? 
              require.context('./media/Background/Main Background/', false, /\.mp4|.png$/).keys().map((url) => url.split("./")[1]) : null,
        loopAll:true,
        modalVid:null
      };
    },  
    
    mounted() {
      // console.log(this.$store.state.feedback);
      this.$store.commit("setActivePage", "App");

      if (document.getElementById("main")) document.getElementById("main").addEventListener("click", this.handleClick);
      // var random = Math.floor(Math.random() * this.dir.length);

      // this.modalVid = require(`./media/Background/Main Background/${this.dir[random].split("./")[1]}`);
      
      // this.randomizeVideo(this.integer());
      this.$refs.bg_video.addEventListener("ended", () => this.handleEnded(num));

      this.carousel();
      this.handleTileSpacing();

      if (this.inactiveState.enabled) this.inactiveStateTimer();
      this.$nextTick(() => {
        if (this.activePage === "App") this.$refs.bg_video.play();
      })

      window.addEventListener('wheel', this.disablewheelzoom, {
        "passive": false
      });
      window.ontouchstart = function(event) {
        if (event.touches.length>1) { //If there is more than one touch
          event.preventDefault();
        }
      }

    }
  };
</script>

<style scoped>
  @import "./css/style.css";
  .top_area_wrapper {
    padding-top: 109.09px;
  }
  #top_side_nav_left {
    margin-right: auto;
  }
  video, img, svg {
    pointer-events: none;
  }
.homeinvisible {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.resetAnalytics {
  position: fixed;
  /* background: black; */
  width: 100px;
  height: 100px;
  right: 0;
  bottom: 0;
  z-index: 100;
}
</style>
