
import Vue from 'vue';
import store from './store.js';
export class Carouseljs {
    constructor() {
        store.commit("setCarouselTransform", store.state.carouselPos * 2160);
    }
    
    pause() {
        // Pause all videos
        return new Promise((res) => {
            if (this.carousel)
                [...this.carousel.children].map(i => [...i.getElementsByTagName("video")]).flat(3).forEach(i => i.pause());
            res();
        });
    }

    play() {
        // Play all videos
        if (document.getElementById(`carousel-${store.state.carouselPos}`)) {
            this.activeVids = [...document.getElementById(`carousel-${store.state.carouselPos}`).children].map(i => i.getElementsByTagName("video")[0]).filter(i => i !== undefined);
            Vue.nextTick(() =>{
                this.activeVids.forEach(i => {
                    i.play();
                });
            })
        }
    }

    init(dir, multiple) {
        // Limit of how many times you can scroll the carousel (based on amount of videos in multiple of {multiple})
        this.limit = Math.ceil(dir.length / multiple) - 1;
        // Overall carousel
        this.carousel = document.getElementById('tile_carousel_page_1');
       
        
        // Position in xpx of how much to move the carousel
        this.pos = 0;
        
        // variable to disable pressing left button
        this.leftPressDisable = false;
        // variable to disable pressing right button
        this.rightPressDisable = false;
        // Calcualtes how many times carousel has moved
        // When page loads, play videos visible and keep the rest paused    
        

        // if (dir.length > multiple) {
        //     // Right button
        //     this.right = document.getElementById("tile_carousel_nav_right");
        //     // Left button
        //     this.left = document.getElementById("tile_carousel_nav_left");

        //     // Remove any past event Listeners
        //     this.right.removeEventListener("mousedown", (e) => this.carouselright(e));
        //     this.left.removeEventListener("mousedown",  (e) => this.carouselleft(e));
        //     // Event listeners to clicking the left and right button
        //     this.right.addEventListener("mousedown", (e) => this.carouselright(e));
        //     this.left.addEventListener("mousedown",  (e) => this.carouselleft(e));
        // }
        
    }

    carouselleft(e) {
        e.stopPropagation();

        // Disable left button carousel
        if (this.leftPressDisable || store.state.carouselPos <= 0) return;


        // Temporary disable button while the carousel moves for the 1000ms
        this.leftPressDisable = true;


        return new Promise((res) => {
            // Carousel position moves 2160px to the right

            // Remembers the last carousel position the user was on
            store.commit("setCarouselPos", store.state.carouselPos - 1);
            store.commit("setCarouselTransform", store.state.carouselPos * 2160);

            // Timeout to let carousel move is 1000ms (1s)
            setTimeout(() => {
                // After 1000ms is up, you can press the left button again
                this.leftPressDisable = false;


          
                // Resolve promise
                res();
            }, 1000)
        })
        
    }

    carouselright(e) {
        e.stopPropagation();
        // Disable right button carousel
        if (this.rightPressDisable || store.state.carouselPos >= this.limit) return;
 
        // Temporary disable button while the carousel moves for the 1000ms
        this.rightPressDisable = true;

        return new Promise((res) => {

            // Carousel position moves 2160px to the left

            // Remembers the last carousel position the user was on
            store.commit("setCarouselPos", store.state.carouselPos + 1);
            store.commit("setCarouselTransform", store.state.carouselPos * 2160);


            // Timeout to let carousel move is 1000ms (1s)
            setTimeout(() => {
                // After 1000ms is up, you can press the right button again
                this.rightPressDisable = false;

                // Carousel has moved 1 time

                // Resolve promise
                res();
            }, 1000)
        })
    }
}